.card {
  position: relative;
  width: 200px;
  height: 230px;
  margin: 5px;
  transform: scale(1);
  transition: 0.5s;
  transform-style: preserve-3d;
}

.flip {
  transform: rotateY(180deg);
}

.front-face {
  transform: rotateY(180deg);
}

.front-face,
.back-face {
  width: 100%;
  height: 100%;

  position: absolute;
  border-radius: 5px;
  background: #1c7ccc;
  backface-visibility: hidden;
}
